import React, { useContext } from 'react';
// import AnchorLink from 'react-anchor-link-smooth-scroll';
import { ThemeContext } from 'providers/ThemeProvider';
import { Container, Button } from 'components/common';
// import zhao_ji from 'assets/images/zhao-ji_auspicious-cranes-left.jpg';
import { Wrapper, SkillsWrapper, Details, Thumbnail } from './styles';
import data from "../../../data/Data";
import ReactMarkdown from 'react-markdown';
import { StaticImage } from "gatsby-plugin-image";
import Link from "gatsby-link";

export const Papers = () => {
  const { theme } = useContext(ThemeContext);

  return (
    <Wrapper id="papers">
      <SkillsWrapper as={Container}>
        <Thumbnail>
          {/*<img src={zhao_ji} alt="Auspicious Cranes by Zhao Ji"/>*/}
          <StaticImage
              src={"../../../assets/images/zhao-ji_auspicious-cranes-left.jpg"}
              alt="Auspicious Cranes by Zhao Ji"
              placeholder="blurred"
              imgStyle={{ objectFit: "contain" }}
              style={{ height: "100%", width: "100%" }}
          />
        </Thumbnail>
        <Details theme={theme}>
          <h1>Papers</h1>
          {data.papers.items.map((item, idx) =>
            <ReactMarkdown key={idx} className={ idx < 3 ? "visible" : "hidden"}>
              {item.title}
            </ReactMarkdown>
          )}
          <Button as={Link} to="/papers">
            More papers | Download
          </Button>
        </Details>
      </SkillsWrapper>
    </Wrapper>
  );
};
